section {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

#mvp-search {
    padding: 0 0 60px 0;
}

#mvp-search input#search {
    padding: 35px;
    border-radius: 0;
    background-color: #F6F6F6;
    font-size: 17px;
    border: none;
    font-family: 'AvenirNextGEOW05-Medium';
}

section#mvp-search a.btn.btn-flat {
    background-color: #1ca6a3 !important;
    height: 70px;
    width: 70px;
    border-radius: 0;
    line-height: 52px;
    color: #fff;
    position: absolute;
    right: 15px;
    top: 0;
    display: inline-block;
    background: url('data:image/svg+xml;base64,') 50% 50% no-repeat;
    background-size: 25px;
}

.mvp-results-toolbar.row {
    margin: 20px 0;
}

.mvp-results-summary {
    font-size: 15px;
    line-height: normal;
    color: #999999;
    font-family: AvenirNextGEOW05-Medium;
}

.mvp-filter p.panel-heading {
    font-size: 18px;
    font-weight: 600;
    line-height: 1.5;
    color: #232323;
    margin: 0 0 10px;
    border-bottom: 1px solid #e1e1e1;
    padding-bottom: 15px;
}

.mvp-filter article.panel {
    margin-bottom: 30px;
}

.mvp-filter p.control.has-icons-left {
    padding: 9px 0;
}

.mvp-filter input[type="checkbox"] {
    margin-right: 13px;
}

.mvp-filter p.control.has-icons-left {
    padding: 14px 0;
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    line-height: 20.8px;
    color: #212529;
}

.mvp-results-entries .card {
    margin-bottom: 20px;
}

.mvp-results-entries .card-img-top {
    border-radius: 0;
}

.mvp-results-entries .card a:hover {
    text-decoration: none;
}

.mvp-results-entries .card a .mvp-category {
    font-family: AvenirNextW05-Light;
    color: #707070;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 0 0 5px;
    font-size: 13px;
}

.mvp-results-entries .card a .mvp-year {
    position: absolute;
    font-size: 16px;
    color: #707070;
    background: #f6f6f6;
    border-radius: 2px;
    padding: 2px 9px;
    top: 13px;
    left: 13px;
}

.mvp-results-entries .card a .card-title {
    font-size: 23px;
    margin-bottom: 5px;
    font-family: AvenirNextGEOW05-Bold;
    line-height: 1.3;
    color: #232323;
}

.mvp-results-entries .card a .card-text {
    font-size: 14px;
    font-family: AvenirNextW05-Regular;
    line-height: 16px;
    margin-top: 20px;
}

.mvp-facet-results {
    margin: 0px 0 20px 0px;
}

.mvp-facet-results .badge-primary {
    color: #fff;
    background-color: #1ca6a3;
    padding: 5px 15px 6px 15px;
    font-family: 'AvenirNextW05-Light';
}

.badge-primary a {
    margin-left: 8px;
    font-size: 10px;
    font-family: 'AvenirNextGEOW05-Medium';
    color: #ffffff;
}

.badge-primary a:hover {
    text-decoration: none;
}

.mvp-pagination a.page-link {
    padding: 3px 6px;
    color: #1ca6a3;
    border-radius: 2px;
}

.mvp-pagination .pagination {
    flex: 0 0 auto;
    list-style: none;
    padding: 0;
    margin: 25px 0px 30px;
}

.mvp-pagination .pagination .page-item.page-next a,
.mvp-pagination .pagination .page-item.page-previous a {
    border: none;
    background: none;
}

.mvp-pagination .page-item.active .page-link {
    background-color: #1ca6a3;
    border: none;
}

.mvp-pagination li.page-item {
    margin: 0px 6px;
}

.mvp-pagination a.page-link {
    padding: 3px 6px;
    color: #1ca6a3;
    border-radius: 2px;
}
